@use "../util" as *;

.header {
  min-height: 40vh;
  background: url(./../../../images/image-hero-mobile.jpg) no-repeat;
  background-size: cover;

  &__logo {
    z-index: 3;
    cursor: pointer;
  }

  &__gradient {
    width: 100%;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.0001) 100%
    );
    mix-blend-mode: normal;
    // opacity: 0.6;
    height: 40%;
  }

  .navbar {
    z-index: 2;
    padding-top: rem(32);
    padding-bottom: rem(32);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav {
    position: absolute;
    right: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    z-index: 2;
    padding: 0 rem(24);
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.0001) 100%
    );
    mix-blend-mode: normal;
    transition: all ease-in-out 0.3s;

    &.open {
      top: 0;
    }

    &__list {
      list-style: none;
      // position: absolute;
      // top: 50px;      opacity: 1;
      width: 100%;
      margin-top: rem(88);
      background: white;
      padding: 0;
      border-radius: rem(8);

      .navlink {
        &:not(:last-child) {
          border-bottom: 1px solid rgba(47, 47, 47, 0.1);
        }

        a {
          text-decoration: none;
          color: #000;
          display: inline-block;
          padding: rem(24);
          font-style: normal;
          font-weight: 500;
          font-size: rem(18);
          line-height: 1.2;
        }
      }
    }
  }

  &__menuControls {
    z-index: 3;
    cursor: pointer;

    .closeMenu {
      display: none;
    }
    .burger {
      display: inline-block;
    }

    &.open {
      .closeMenu {
        display: inline-block;
      }
      .burger {
        display: none;
      }
    }
  }
}
